<template>
	<v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
			<v-btn
        v-on="on"
        depressed
        color="primary lighten-3">
        konfirmasi pembayaran
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="justify-center">
        Konfirmasi Pembayaran
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text>
          <v-text-field
            v-model="data.nama_rek"
            label="Nama Rekening"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 
          <v-text-field
            v-model="data.no_rek"
            label="Nomor Rekening"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 
          <v-text-field
            v-model="data.catatan"
            label="Catatan"
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field> 
          <v-file-input
            show-size
            counter
            label="Bukti Transfer"
            accept="image/*"
            dense
            v-model="data.gambar"
          ></v-file-input>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        data: {
          nama_rek: '',
          no_rek: '',
          catatan: '',
          gambar: null
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil disimpan',
          error: 'Data Gagal disimpan',
          visible: false
        }
      }
    },

    components: {
      Notif
    }, 

    computed: {
      ...mapState('dataIncubatorOrder', ['isLoading', 'status'])
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          const data = new FormData()
          data.append('no_rekening', this.data.no_rek)
          data.append('nama_rekening', this.data.nama_rek)
          data.append('catatan', this.data.catatan)
          data.append('gambar', this.data.gambar)

          this.$store.dispatch('dataIncubatorOrder/postDataKonfirmasiPembayaran', data)
          this.$store.dispatch('dataIncubatorOrder/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorOrder/updateStatus', null)
      }
    }
  }
</script>