<template>
  <v-row>
    <v-col 
      v-for="item in data" :key="item.id"
      cols="12" sm="6">
      <v-card>
        <v-card-title class="justify-center font-weight-bold text-uppercase">
          PAKET {{ item.paket.nama }}
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <div>Total Pembayaran</div>
          <h1 class="yellow--text text--darken-3 mb-3">
            IDR
            {{ Intl.NumberFormat('id-ID', { maximumSignificantDigits: 4, style: 'currency', currency: 'IDR' }).format(item.paket.harga) }}
          </h1>
          <div>Metode Pembayaran <b>Transfer</b></div>
          <b>Bank Rakyat Indonesia</b>
          <div class="red--text text--lighten-3 mb-3">
            <div>AN. ABP INCUBATOR</div>
            <h2>9988 0075 xxxx xxxx</h2>
          </div>
          <div class="d-flex align-center">
            <v-icon class="mr-2">mdi-calendar-outline</v-icon>
            Tanggal Pemesanan
          </div>
          <h3 class="primary--text mb-3">
            {{tanggal(item.created_at)}}
          </h3>
          <center v-if="item.status_pembayaran === '0'">
            <Konfirmasi :data="item" />
          </center>
          <div v-else>
            <div class="d-flex align-center">
              <v-icon class="mr-2">mdi-clock-outline</v-icon>
              Sisa Masa Aktif Akun
            </div>
            <h3 class="primary--text">
              20 Hari
            </h3>
          </div>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import Konfirmasi from './Konfirmasi'
export default {
  props: ['data'],
  components: {
    Konfirmasi
  },
  methods: {
    tanggal(val) {
      const date = new Date(val)
      const dd = date.getDate()
      const mm = date.getMonth() + 1
      const yy = date.getFullYear()

      return `${dd}-${mm}-${yy}`
    }
  },
}
</script>

